                    :value="item.cate">
                  </el-option>
                </el-select>
              </div>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="grid-content bg-purple-light">
            <el-form-item label="商品名称" prop="name">
              <el-input v-model="name"></el-input>
