      fileList: [],
      cates: [
        { rank: 1, cate: "digital", cate_zh: "手机数码" },
        { rank: 2, cate: "office", cate_zh: "电脑办公" },
        { rank: 3, cate: "electrical", cate_zh: "家用电器" },
        { rank: 4, cate: "beauty", cate_zh: "美妆护肤" },
        { rank: 5, cate: "clean", cate_zh: "个人清洁" },
        { rank: 6, cate: "car", cate_zh: "汽车生活" },
        { rank: 7, cate: "clothe", cate_zh: "男装女装" },
        { rank: 8, cate: "maternal", cate_zh: "母婴儿童" },
        { rank: 9, cate: "book", cate_zh: "图书音像" },
        { rank: 10, cate: "sport", cate_zh: "运动户外" },
        { rank: 11, cate: "fresh", cate_zh: "食品生鲜" },
        { rank: 12, cate: "drink", cate_zh: "饮料酒水" },
        { rank: 13, cate: "gem", cate_zh: "钟表珠宝" },
        { rank: 14, cate: "music", cate_zh: "玩具乐器" },
        { rank: 15, cate: "pet", cate_zh: "宠物生活" }
      ]
    };
  },
  // watch:{
  //   cate:function(a,b){
  //     console.log(a,b)
  //   }
  // },
  
